}

.hover\:text-\[var\(--text-primary\)\]:hover {
  color: var(--text-primary);
}

.hover\:text-\[var\(--text-secondary\)\]:hover {
  color: var(--text-secondary);
}

.hover\:underline:hover {
  text-decoration-line: underline;
}

.hover\:shadow-\[var\(--shadow-sm\)\]:hover {
  --tw-shadow-color: var(--shadow-sm);
  --tw-shadow: var(--tw-shadow-colored);
}

.hover\:bg-token-inset:hover { background-color: var(--bg-inset); }

.hover\:bg-token-canvas:hover { background-color: var(--bg-canvas); }

.hover\:text-token-primary:hover { color: var(--text-primary); }

.hover\:border-token-border-strong:hover { border-color: var(--border-strong); }

.focus\:border-\[var\(--primary\)\]:focus {
  border-color: var(--primary);
}

.focus\:border-\[var\(--primary-400\)\]:focus {
  border-color: var(--primary-400);
}

.focus\:border-transparent:focus {
  border-color: transparent;
}

.focus\:shadow-\[0_0_0_3px_rgba\(220\2c 38\2c 38\2c 0\.1\)\]:focus {
  --tw-shadow: 0 0 0 3px rgba(220,38,38,0.1);
  --tw-shadow-colored: 0 0 0 3px var(--tw-shadow-color);
  box-shadow: var(--tw-ring-offset-shadow, 0 0 #0000), var(--tw-ring-shadow, 0 0 #0000), var(--tw-shadow);
}

.focus\:shadow-\[0_0_0_3px_rgba\(37\2c 99\2c 235\2c 0\.1\)\]:focus {
  --tw-shadow: 0 0 0 3px rgba(37,99,235,0.1);
  --tw-shadow-colored: 0 0 0 3px var(--tw-shadow-color);
  box-shadow: var(--tw-ring-offset-shadow, 0 0 #0000), var(--tw-ring-shadow, 0 0 #0000), var(--tw-shadow);
}

.focus\:shadow-\[0_0_0_3px_rgba\(5\2c 150\2c 105\2c 0\.1\)\]:focus {
  --tw-shadow: 0 0 0 3px rgba(5,150,105,0.1);
  --tw-shadow-colored: 0 0 0 3px var(--tw-shadow-color);
  box-shadow: var(--tw-ring-offset-shadow, 0 0 #0000), var(--tw-ring-shadow, 0 0 #0000), var(--tw-shadow);
}

.focus\:outline-none:focus {
  outline: 2px solid transparent;
  outline-offset: 2px;
}

.focus\:ring-2:focus {
  --tw-ring-offset-shadow: var(--tw-ring-inset) 0 0 0 var(--tw-ring-offset-width) var(--tw-ring-offset-color);
  --tw-ring-shadow: var(--tw-ring-inset) 0 0 0 calc(2px + var(--tw-ring-offset-width)) var(--tw-ring-color);
  box-shadow: var(--tw-ring-offset-shadow), var(--tw-ring-shadow), var(--tw-shadow, 0 0 #0000);
